import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './Slider.scss';

const Slider = ({ type, subtype, steps, handleSize }) => {
  const [value, setValue] = useState(type === 'discrete' ? 0 : 50);
  const [range, setRange] = useState([0, 100]);

  const handleChange = (event) => {
    const newValue = Number(event.target.value);
    if (subtype === 'range') {
      setRange([newValue, range[1]]);
    } else {
      setValue(newValue);
    }
  };

  const handleRangeChange = (event, index) => {
    const newValue = Number(event.target.value);
    if (index === 0) {
      setRange([newValue, 100]);
    } else {
      setRange([range[0], newValue]);
    }
  };

  return (
    <div className={`slider slider-${handleSize}`}>
      {subtype === 'range' ? (
        <>
          <input
            type="range"
            className="slider-input range"
            min="0"
            max="100"
            value={range[0]}
            onChange={(e) => handleRangeChange(e, 0)}
            step={10}
          />
          <div
            className="tooltip"
            style={{ left: `${(range[0] / 100) * 100}%` }}
          >
            {range[0]}
          </div>
          <div
            className="tooltip"
            style={{ left: `${(range[1] / 100) * 100}%` }}
          >
            {range[1]}
          </div>
        </>
      ) : (
        <>
          <input
            type="range"
            className={`slider-input ${subtype}`}
            min="0"
            max={type === 'discrete' ? steps : 100}
            value={value}
            onChange={handleChange}
            step={type === 'discrete' ? 1 : undefined}
          />
          <div
            className="tooltip"
            style={{ left: `${(value / (type === 'discrete' ? steps : 100)) * 100}%` }}
          >
            {value}
          </div>
        </>
      )}
      <div className="slider-values">
        {subtype === 'range' ? `${range[0]} - ${range[1]}` : value}
      </div>
    </div>
  );
};

Slider.propTypes = {
  type: PropTypes.oneOf(['continuous', 'discrete']),
  subtype: PropTypes.oneOf(['single', 'range']),
  steps: PropTypes.number,
  handleSize: PropTypes.oneOf(['size_24', 'size_32']),
};

Slider.defaultProps = {
  type: 'continuous',
  subtype: 'single',
  steps: 10,
  handleSize: 'size_24',
};

export default Slider;
