___CSS_LOADER_EXPORT___.push([module.id, `.slider {
  position: relative;
  width: 100%; }
  .slider .slider-input {
    width: 100%;
    -webkit-appearance: none;
    background: transparent;
    position: absolute; }
    .slider .slider-input.range:nth-of-type(1) {
      z-index: 1; }
    .slider .slider-input.range:nth-of-type(2) {
      z-index: 2; }
    .slider .slider-input::-webkit-slider-runnable-track {
      height: 8px;
      background: #ddd;
      border-radius: 4px; }
    .slider .slider-input::-moz-range-track {
      height: 8px;
      background: #ddd;
      border-radius: 4px; }
    .slider .slider-input::-ms-track {
      height: 8px;
      background: #ddd;
      border-radius: 4px; }
    .slider .slider-input::-webkit-slider-thumb {
      -webkit-appearance: none;
      background-color: green;
      height: 20px;
      width: 20px;
      border-radius: 50%;
      border: 4px solid white;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
      margin-top: -6px; }
    .slider .slider-input::-moz-range-thumb {
      background-color: green;
      height: 20px;
      width: 20px;
      border-radius: 50%;
      border: 4px solid white;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.3); }
    .slider .slider-input::-ms-thumb {
      background-color: green;
      height: 20px;
      width: 20px;
      border-radius: 50%;
      border: 4px solid white;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.3); }
    .slider .slider-input::-webkit-slider-thumb:hover, .slider .slider-input::-moz-range-thumb:hover, .slider .slider-input::-ms-thumb:hover {
      background-color: #00b000; }
    .slider .slider-input::-webkit-slider-thumb:focus, .slider .slider-input::-moz-range-thumb:focus, .slider .slider-input::-ms-thumb:focus {
      border: 4px solid #00b000; }
  .slider .tooltip {
    position: absolute;
    top: -30px;
    background: black;
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    transform: translateX(-50%);
    white-space: nowrap; }
  .slider .slider-values {
    text-align: center;
    margin-top: 50px;
    /* Adjusted margin to position below slider */
    position: relative;
    top: 20px; }
